.loader-container{
    width: 100%;
    position: relative;
}
.loader-spinner{
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 100px;
  -webkit-animation: spin 5s linear 0s infinite normal;
          animation: spin 5s linear 0s infinite normal;
  height: 100px;
  opacity: 0.1;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

@-webkit-keyframes spin {
    to {
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }

@keyframes spin {
    to {
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }


